@import url(https://fonts.googleapis.com/css2?family=Gwendolyn:wght@700&display=swap);
body{
    background: rgb(36, 1, 68);
    color: white;
}



*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

* a, ul{
    text-decoration: none;
    list-style: none;
}

.nav-logo{
    font-family: 'Gwendolyn', cursive;
    font-size: 70px;
}

.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 4rem;
}


.nav ul{
    display: flex;
}

.nav ul li a{
    font-size: 25px;
    color: #fff;
    padding: 0 19px;
}

.nav ul li a:hover{
    color: aqua;
}

.nav ul li{
    padding: 6px 0;
}

.nav-toggle{
    display: none !important;
  height: 26px;
  width: 32px;
  position: absolute;
  top: 17px;
  right: 2rem;
  z-index: 2;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.nav-toggle-t .nav-toggle1{
    /* position: absolute;
    left: 0;
    top: 11px; */
    transform: rotate(45deg) !important;
    transition: all 0.1s ease;
}
.nav-toggle1{
    position: absolute;
    transform-origin: 0% 0%;
    transform: rotate(0);
    transition: all 0.1s ease;
    margin-bottom: 5px;
}
.nav-toggle-t .nav-toggle2{
    width: 0%;
    transition: all .1s ease;
}
.nav-toggle2{
    position: absolute;
    top: 42%;
    overflow-x: hidden;
    width: 100%;
}
.nav-toggle-t .nav-toggle3{
    /* position: absolute;
    left: 0;
    top: 11px; */
    transform: rotate(-45deg) ;
    transition: all 0.1s ease;
}
.nav-toggle3{
    position: absolute;
    bottom: 0;
    transform-origin: 0% 100%;
    transform: rotate(0);
    transition: all 0.1s ease;
    margin-top: 5px;
}

.nav-toggle div{
    display: block;
  height: 4px;
  width: 100%;
  border-radius: 10px;
    background: white;
}


@media screen and (max-width:768px) {
    .navbar{
        padding: 0 2rem;
    }
    .nav-logo{
        font-size: 45px;
    }
    .nav-toggle{
        display: block !important;
    }

    .nav-nav{
        flex-direction: column;
        position: absolute;
        top: 54px;
        right: 30%;
        left: 0;
        text-align: center;
        width: 100%;
        z-index: 100;
        overflow-y: hidden;
        height: 0 ;
        transition: all .3s;
        background: rgba(94, 14, 241, 0.9);
    }
    .show{
        height: 180px !important;
    }
}




.homepage{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.homepage p{
    font-size: 45px
}

.h-btn{
    padding: 15px 20px;
    background: transparent;
    color: #fff;
    border: solid 1px #fff;
    border-radius: 10px;
    font-size: 19px;
    margin: 0 15px;
}
.h-btn:active{
    transform: scale(1.01);
}
.h-btn:hover{
    background: aqua;
}

.left-side{
    height: 600px;
    width: 600px;
    position: relative;
    padding-right: 2rem;
}

.left-side div{
    overflow-y: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.left-side div p{
    text-align: center;
    width: 500px;
}

.left-side p{
    position: absolute;
    top: 200px;
    color: #000;
    z-index: 15 !important;
}

.left-side-img{
    position: absolute;
    top: 0;
    left: 0;
    z-index: -10 !important;
    width: 100% !important;
    height: 100% !important;
}

.left-side-bottom{
    position: relative;
    bottom: 50px;
}






@media screen and (max-width:768px) {
    .left-side{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100% !important;
        height: 400px;
    }
    .left-side div{
        padding: 0 !important;
        text-align: center;
    }
    .left-side-img{
        width: 350px !important;
        height: 300px !important;
    }
    .left-side p{
        top: 100px !important;
        position: absolute;
        left: 8%;
        font-size: 27px;
        width: 280px !important;
    }
}
a  {
    text-decoration: none;
}

.about{
    display: flex;
    justify-content: space-around;
    padding: 0 4rem;
    padding-top: 3rem;
}
    
.about-left img{
    height: 40vh;
}

.about-right-div{
    padding: 2rem 4rem;
    width: 800px;
    color: aqua;
    animation: animate-color 4s 1;
    color: #fff;
    background: linear-gradient(to bottom right, blue, aqua);
    box-shadow: -5px -5px 30px 5px aqua, 5px 5px 30px 5px blue !important;
    border-radius: 20px;
}
.about-right h2{
    text-align: center;
    font-weight: 500;
    font-size: 30px;
}
.about-right p{
    font-size: 25px;
}

.about-icons{
    text-align: center;
    padding: 0.5rem 0;
}

.about-icons i{
    font-size: 45px;
    padding: 0 2rem;
    cursor: pointer;
}

.a-btn{
    padding: 15px 20px;
    background: transparent;
    color: #fff;
    border: solid 1px #fff;
    border-radius: 10px;
    font-size: 19px;
    margin: 0 15px;
    cursor: pointer;
}
.a-btn:hover{
    background: rgba(94, 14, 241, 0.9);
}
.a-btn:active{
    transform: scale(1.01)
}


/* Cube */
@keyframes grow1 {
    from {
      width: 0%
    }
    to {
      width: 80%;
    }
}
@keyframes grow2 {
    from {
        width: 0%
      }
      to {
        width: 60%;
      }
}
@keyframes grow3 {
    from {
        width: 0%
      }
      to {
        width: 50%;
      }
}
@keyframes grow4{
    from {
        width: 0%
      }
      to {
        width: 67%;
      }
}
  #container {
    position: relative;
    width: 90%;
    top: 30px;
    left: 50%;
    transform: translate3d(-50%, -50%, 0);
    padding-bottom: 15px;
  }
  input {
    display: none;
  }
  label {
    position: relative;
    display: block;
    width: 100%;
    height: 40px;
    background: transparent;
    border-radius: 0.4rem;
    border: 2px solid white;
    overflow: hidden;
    box-shadow: -2px 0 8px 0 rgba(white, 0.6);
  }
  #fill {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 0%;
    box-shadow: 0 0 8px 1px white inset;
  }
  .fill-color-1{
    background: linear-gradient(to bottom, #f3a839 35%, #f14b09) !important;
    animation: grow1 2s forwards;
  }
  .fill-color-2{
    background: linear-gradient(to bottom, #0cffeb 35%, #0059fd);
    animation: grow2 2s forwards;
  }
  .fill-color-3{
    background: linear-gradient(to bottom, #eeff04 35%, #fcc100);
    animation: grow3 2s forwards;
  }
  .fill-color-4{
    background: linear-gradient(to bottom, #A3E2EF 35%, #4F9CC0);
    animation: grow4 2s forwards;
  }
  #fill:before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background: url(/static/media/bubbles-mask.c5793634.gif);
    mix-blend-mode: overlay;
    opacity: 0.5;
  }


/* //////////////////\\\\\\\\\\\\\\\\\\\\\\\ */


@media screen and (max-width:768px) {
    .about{
        flex-direction: column;
        justify-content: center;
        padding: 0 0 !important;
    }
    .about-right{
        padding-top: 2rem;
    }
    .about-right-div{
        width: 94% !important;
        padding: 2rem 2rem;
        margin: auto;
    }
    .about-right-div p{
        font-size: 18px;
    }
    .about-icons i{
        font-size: 30px;
        padding: 0 1rem;
    }
    .a-btn{
        width: 90%;
        margin-bottom: 10px;
    }
}
